import axios from "@/axios.js"

export default {
  fetchBatches({ commit }) {
    return new Promise((resolve, reject) => {
      axios.get("/admin/batches")
        .then((response) => {
          commit('SET_BATCHES', this.state.responseDataPath ? response[this.state.responseDataPath].data : response.data)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  fetchBatch({ commit }, batchId) {
    return new Promise((resolve, reject) => {
      axios.get(`/admin/batches/${batchId}`)
        .then((response) => {
          commit('SET_BATCH', this.state.responseDataPath ? response[this.state.responseDataPath].data : response.data)
          resolve(this.state.responseDataPath ? response[this.state.responseDataPath] : response)
        })
        .catch((error) => { reject(error) })
    })
  },
  addBatch({ commit }, data) {
    return new Promise((resolve, reject) => {
      const attributes = {...data, status: data.status.value }
      axios.post("/admin/batches", { attributes })
        .then((response) => {
          commit('ADD_BATCH', this.state.responseDataPath ? response[this.state.responseDataPath].data : response.data)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  updateBatch({ commit }, data) {
    return new Promise((resolve, reject) => {
      axios.patch(`/admin/batches/${data.id}`, {data: data})
        .then((response) => {
          commit('UPDATE_BATCH', Object.assign(data, {id: response.data.id}))
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  removeBatch({ commit }, batchId) {
    return new Promise((resolve, reject) => {
      axios.delete(`/admin/batches/${batchId}`)
        .then((response) => {
          commit('REMOVE_BATCH', batchId)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  fetchOrdersReport({ commit }, batchId) {
    return new Promise((resolve, reject) => {
      axios.get(`/admin/batches/${batchId}/orders-report`)
        .then((response) => {
          commit('SET_ORDERS_REPORT', this.state.responseDataPath ? response[this.state.responseDataPath].data : response.data)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
}
