<template>
  <div id="page-user-view">

    <vs-alert color="danger" :title="$t('batch.notFound.title')" :active.sync="batch_not_found">
      <span>{{ $t('batch.notFound.message', { batchId: $route.params.batchId }) }} </span>
    </vs-alert>

    <div id="user-data" v-if="batch_data">

      <vx-card title="Batch" class="mb-base">

        <div class="vx-row">

          <!-- Information - Col 1 -->
          <div class="vx-col flex-1" id="account-info-col-1">
            <table>
              <tr>
                <td class="font-semibold">{{ $t('batch.id') }}</td>
                <td>{{ batch_data.id }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ $t('batch.year') }}</td>
                <td>{{ batch_data.year }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ $t('batch.status') }}</td>
                <td>{{ batch_data.status ? 'Ativo' : 'Inativo' }}</td>
              </tr>
            </table>
          </div>
          <!-- /Information - Col 1 -->

          <!-- Information - Col 2 -->
          <div class="vx-col flex-1" id="account-info-col-2">
            <table>
              <tr>
                <td class="font-semibold">{{ $t('batch.dateStart') }}</td>
                <td>{{ batch_data.dateStart | moment("DD/MM/YYYY") }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ $t('batch.dateEnd') }}</td>
                <td>{{ batch_data.dateEnd | moment("DD/MM/YYYY") }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ $t('batch.datePayment') }}</td>
                <td>{{ batch_data.datePayment | moment("DD/MM/YYYY") }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ $t('batch.datePayment2') }}</td>
                <td>{{ batch_data.datePayment2 | moment("DD/MM/YYYY") }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ $t('batch.dateDelivery') }}</td>
                <td>{{ batch_data.dateDelivery | moment("DD/MM/YYYY") }}</td>
              </tr>
            </table>
          </div>
          <!-- /Information - Col 2 -->
          <div class="vx-col w-full flex" id="account-manage-buttons">
            <vs-button icon-pack="feather" icon="icon-edit" class="mr-4" :to="{name: 'admin-batch-edit', params: { batchId: $route.params.batchId }}">{{ $t('button.edit') }}</vs-button>
            <!-- <vs-button icon-pack="feather" icon="icon-list" class="mr-4" :to="{name: 'admin-orders-report', params: { batchId: $route.params.batchId }}">{{ $t('button.order.report') }}</vs-button> -->
            <!-- <vs-button icon-pack="feather" icon="icon-list" class="mr-4" :to="{name: 'admin-order-list', params: { batchId: $route.params.batchId }}">{{ $t('button.order.list') }}</vs-button> -->
          </div>
          <div class="vx-col w-full flex" id="account-manage-buttons">
          </div>

        </div>

      </vx-card>
    </div>
  </div>
</template>

<script>
import moduleBatchManagement from '@/store/batch-management/moduleBatchManagement.js'

export default {
  data() {
    return {
      batch_data: null,
      batch_not_found: false,
    }
  },
  methods: {
    confirmDeleteRecord() {
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: `Confirm Delete`,
        text: `You are about to delete "${this.batch_data.username}"`,
        accept: this.deleteRecord,
        acceptText: "Delete"
      })
    },
    deleteRecord() {
      /* Below two lines are just for demo purpose */
      this.$router.push({name:'app-user-list'});
      this.showDeleteSuccess()

      /* UnComment below lines for enabling true flow if deleting user */
      // this.$store.dispatch("batchManagement/removeRecord", this.batch_data.id)
      //   .then(()   => { this.$router.push({name:'app-user-list'}); this.showDeleteSuccess() })
      //   .catch(err => { console.error(err)       })
    },
    showDeleteSuccess() {
      this.$vs.notify({
        color: 'success',
        title: 'User Deleted',
        text: 'The selected user was successfully deleted'
      })
    }
  },
  created() {
    // Register Module UserManagement Module
    if(!moduleBatchManagement.isRegistered) {
      this.$store.registerModule('batchManagement', moduleBatchManagement)
      moduleBatchManagement.isRegistered = true
    }

    const batchId = this.$route.params.batchId
    this.$store.dispatch("batchManagement/fetchBatch", batchId)
      .then(res => { this.batch_data = res.data.attributes })
      .catch(err => {
        if(err.response.status === 404) {
          this.batch_not_found = true
          return
        }
        console.error(err) })
  }
}

</script>

